import Auth from './pages/Auth.js';
import Category from './pages/Category'
import { lazy } from 'react';
import { CATEGORY_ROUTE, COUNTRY_ROUTE, LOGIN_ROUTE, NEWS_ROUTE, ORDERS_ROUTE, PRODUCTS_ROUTE, PRODUCT_ROUTE, USERS_ROUTE } from './utils/consts.js';

const Producer = lazy(() => import('./pages/Producer.js'));
const Product = lazy(() => import('./pages/Product.js'));
const MainPage = lazy(() => import('./pages/MainPage.js'));
const Products = lazy(() => import('./pages/Products.js'));
const Orders = lazy(() => import('./pages/Orders.js'));
const Users = lazy(() => import('./pages/Users.js'));
const News = lazy (() => import('./pages/News.js'))

export const auth_routes = [
    {
        path : CATEGORY_ROUTE,
        Component : Category
    },
    {
        path : COUNTRY_ROUTE,
        Component : Producer
    },
    {
        path : PRODUCTS_ROUTE,
        Component : Products
    },
    {
        path : PRODUCT_ROUTE + '/:id',
        Component : Product
    },
    {
        path : ORDERS_ROUTE,
        Component : Orders
    },
    {
        path : USERS_ROUTE,
        Component : Users
    },
    {
        path:NEWS_ROUTE,
        Component:News
    }
];

export const login_routes = [
    {
        path : LOGIN_ROUTE,
        Component : Auth
    }
];